<template>
  <div class="comingSoon-wrap">
    <ul
      v-infinite-scroll="loadMore"
      infinite-scroll-disabled="loading"
      infinite-scroll-distance="0"
      infinite-scroll-immediate-check="false"
    >
      <li
        class="comingSoonFilm-item"
        v-for="data in $store.state.comingList"
        :key="data.filmId"
        @click="handleClick(data.isPresale, data.filmId)"
      >
        <div class="comingSoonFilm-wrap">
          <div
            data-v-21c202bd=""
            class="lazy-img comingSoonFilm-img"
            alt="film"
            style="width: 66px; height: 94px; background: rgb(249, 249, 249); float: left;"
          >
            <div
              data-v-21c202bd=""
              class="padding"
              style="width: 66px; height: 94px; background: rgb(249, 249, 249);"
            >
              <img
                data-v-21c202bd=""
                src="data:image/png;base64,"
                width="64px"
                alt="img"
              />
            </div>
            <div
              data-v-21c202bd=""
              class="lazy-img-wrap"
              style="width: 66px; height: 94px; background: rgb(249, 249, 249); opacity: 1; display: block;"
            >
              <img data-v-21c202bd="" :src="data.poster" class="target-img" />
            </div>
          </div>
          <div class="comingSoonFilm-info" style="float: left;">
            <div class="comingSoonFilm-name info-col">
              <span class="name">{{ data.name }}</span
              ><span class="item">{{ data.filmType.name }}</span>
            </div>
            <div class="comingSoonFilm-grade info-col">
              &nbsp;
            </div>
            <div class="comingSoonFilm-actors info-col">
              <span class="label">主演：{{ data.actors | actorfilter }}</span>
            </div>
            <div class="comingSoonFilm-detail info-col">
              <span class="label"
                >上映日期：{{ data.premiereAt | timefilter }}</span
              >
            </div>
          </div>
        </div>
      </li>
      <!-- <li
        v-for="data in $store.state.comingList"
        :key="data.filmId"
        @click="handleClick(data.isPresale, data.filmId)"
      >
        <img :src="data.poster" />
        <h3>{{ data.name }}</h3>
        <p>观众评分{{ data.grade }}</p>
        <p>主演：{{ data.actors | actorfilter }}</p>
      </li> -->
    </ul>
  </div>
</template>
<script>
import { MessageBox } from "mint-ui";
import { InfiniteScroll } from "mint-ui";
import Vue from "vue";
Vue.use(InfiniteScroll);
Vue.filter("actorfilter", data => {
  var newlist = data.map(item => item.name);
  return newlist.join(" ");
});
//unix时间转换为标准时间
Vue.filter("timefilter", unixtime => {
  var unixTimestamp = new Date(unixtime * 1000);
  var time = unixTimestamp
    .toLocaleString()
    .split(" ")[0]
    .split("/");
  return time[1] + "月" + time[2] + "日";
});
export default {
  data() {
    return {
      loading: false,
      ishow: true
    };
  },
  mounted() {
    this.$store.commit("isNowplaying", false);
    if (this.$store.state.comingList.length === 0) {
      this.$store.dispatch("getComingListAction");
    } else {
      console.log("取出缓存");
    }
  },
  methods: {
    handleClick(data, id) {
      if (!data) {
        MessageBox({
          title: "提示",
          message: "没有排片，看看其他电影吧",
          showCancelButton: true
        }).then(res => {
          if (res == "confirm") {
            this.$router.push("/film/nowplaying");
          }
        });
      }
      //编程式导航--路径跳转
      // this.$router.push(`/detail/${id}`)
      //编程式导航--名字跳转
      this.$router.push({ name: "detail", params: { id } });
    },
    loadMore() {
      this.loading = true; //禁用loading
      if (this.$store.state.comingList.length === this.total) {
        this.isShow = false;
        return;
      }
      this.$store.dispatch("getMoreComingListAction").then(() => {
        this.loading = false;
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.comingSoon-wrap ul {
  list-style: none;
  padding: 0;
  margin-left: 15px;
  margin-bottom: 0;
  margin-top: 0;
  li {
    padding: 15px 15px 15px 0;
    height: 94px;
    position: relative;
    .comingSoonFilm-wrap {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: start;
      -ms-flex-pack: start;
      justify-content: flex-start;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
      .lazy-img {
        position: relative;
        .lazy-img-wrap {
          width: 100%;
          background: #fff;
          position: absolute;
          overflow: hidden;
          top: 0;
          opacity: 0;
          .target-img[data-v-21c202bd] {
            width: 100%;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            -ms-transform: translateY(-50%);
            -moz-transform: translateY(-50%);
            -webkit-transform: translateY(-50%);
            -o-transform: translateY(-50%);
          }
        }
      }
    }
    .comingSoonFilm-info {
      width: calc(100% - 116px);
      padding: 0 10px;
      .info-col {
        overflow: hidden;
        -o-text-overflow: ellipsis;
        text-overflow: ellipsis;
        white-space: nowrap;
        width: 100%;
        .name {
          max-width: calc(100% - 25px);
          color: #191a1b;
          font-size: 16px;
          height: 22px;
          line-height: 22px;
          margin-right: 5px;
          overflow: hidden;
          -o-text-overflow: ellipsis;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        .item {
          font-size: 9px;
          color: #fff;
          background-color: #d2d6dc;
          height: 14px;
          line-height: 14px;
          padding: 0 2px;
          border-radius: 2px;
        }
      }
      .label {
        font-size: 13px;
        margin-top: 4px;
        color: #797d82;
      }
    }
  }
}
</style>
